'use client';

import { createContext, useContext, useState, type ReactNode } from 'react';

export interface GameQuery {
  genre: number | null;
  platform: number | null;
  sortOrder: string;
  searchText: string;
}

interface GameQueryContextType {
  gameQuery: GameQuery;
  setGameQuery: (query: GameQuery) => void;
  setGenreId: (genreId: number | null) => void;
  setPlatformId: (platformId: number | null) => void;
  setSortOrder: (sortOrder: string) => void;
  setSearchText: (searchText: string) => void;
}

const GameQueryContext = createContext<GameQueryContextType | undefined>(undefined);

export function GameQueryProvider({ children }: { children: ReactNode }) {
  const [gameQuery, setGameQuery] = useState<GameQuery>({
    genre: null,
    platform: null,
    sortOrder: '',
    searchText: '',
  });

  const setGenreId = (genreId: number | null) => 
    setGameQuery(prev => ({ ...prev, genre: genreId }));

  const setPlatformId = (platformId: number | null) => 
    setGameQuery(prev => ({ ...prev, platform: platformId }));

  const setSortOrder = (sortOrder: string) => 
    setGameQuery(prev => ({ ...prev, sortOrder }));

  const setSearchText = (searchText: string) => 
    setGameQuery(prev => ({ ...prev, searchText }));

  return (
    <GameQueryContext.Provider value={{ gameQuery, setGameQuery, setGenreId, setPlatformId, setSortOrder, setSearchText }}>
      {children}
    </GameQueryContext.Provider>
  );
}

export const useGameQuery = () => {
  const context = useContext(GameQueryContext);
  if (!context) throw new Error('useGameQuery must be used within a GameQueryProvider');
  return context;
};
