'use client';

import { useRef } from 'react';
import { BsSearch } from 'react-icons/bs';
import { useGameQuery } from '../context/GameQueryContext';

export default function SearchInput() {
  const { setSearchText } = useGameQuery();
  const ref = useRef<HTMLInputElement>(null);

  const handleSubmit = (event: React.FormEvent) => {
    event.preventDefault();
    if (ref.current) {
      setSearchText(ref.current.value);
    }
  };

  return (
    <form onSubmit={handleSubmit} className="w-full">
      <div className="relative w-full">
        <BsSearch className="absolute left-4 top-1/2 -translate-y-1/2 text-gray-400 h-4 w-4 pointer-events-none" />
        <input
          ref={ref}
          type="search"
          placeholder="Search games..."
          className="w-full pl-10 pr-4 py-2 bg-[#202020] hover:bg-[#2b2b2b] focus:bg-white focus:text-gray-900 rounded-full text-white placeholder-gray-500 focus:outline-none transition-colors duration-200"
        />
      </div>
    </form>
  );
}
